import "./scss/app.scss";
import React, { Component, Fragment, Suspense } from "react";
import { Router, Route } from "react-router-dom";
import { connect } from "react-redux";
import history from "../history";

// import Animation from "./Animation";
import Header from "./Header";
import Principal from "./Principal";
import About from "./About";
import Portfolio from "./Portfolio";
import Equipment from "./Equipment";
import Contact from "./Contact";

import Max from "./about/Max";
import Francisco from "./about/Francisco";

import SongCreate from "./songs/SongCreate";
import SongEdit from "./songs/SongEdit";
import SongList from "./songs/SongList";
import SongDelete from "./songs/SongDelete";

class App extends Component {
  state = {
    isVisible: true,
    isNone: false
  };

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        isVisible: false
      });
    }, 8000);
    setTimeout(() => {
      this.setState({
        isNone: true
      });
    }, 8500);
  }

  render() {
    document.body.style.backgroundImage = `url(${this.props.imagen})`;
    return (
      <Fragment>
        {/* <Animation visible={this.state.isVisible} none={this.state.isNone} /> */}
        <Router basename={"/sonicden"} history={history}>
          <div
            className="backImage"
            // style={{ backgroundImage: `url(${this.props.imagen})` }}
          >
            <Suspense fallback="loading">
              <Header />
            </Suspense>
            {/* <Route path="/" exact component={Principal} />
            <Route path="/about" exact component={About} />
            <Route path="/about/FranciscOroz" exact component={Francisco} />
            <Route path="/about/MaxArnold" exact component={Max} />
            <Route path="/portfolio" exact component={Portfolio} />
            <Route path="/equipment" exact component={Equipment} />
            <Route path="/contact-us" exact component={Contact} />

            <Route path="/song/list" exact component={SongList} />
            <Route path="/song/new" exact component={SongCreate} />
            <Route path="/song/edit/:id" exact component={SongEdit} />
            <Route path="/song/delete/:id" exact component={SongDelete} /> */}

            <Route
              path={`${process.env.PUBLIC_URL}/`}
              exact
              component={Principal}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/about`}
              exact
              component={About}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/about/FranciscOroz`}
              exact
              component={Francisco}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/about/MaxArnold`}
              exact
              component={Max}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/portfolio`}
              exact
              component={Portfolio}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/equipment`}
              exact
              component={Equipment}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/contact-us`}
              exact
              component={Contact}
            />

            <Route
              path={`${process.env.PUBLIC_URL}/song/list`}
              exact
              component={SongList}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/song/new`}
              exact
              component={SongCreate}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/song/edit/:id`}
              exact
              component={SongEdit}
            />
            <Route
              path={`${process.env.PUBLIC_URL}/song/delete/:id`}
              exact
              component={SongDelete}
            />
          </div>
        </Router>
      </Fragment>
    );
  }
}

const mapStateToProps = state => {
  return { imagen: state.background.imagen };
};

export default connect(mapStateToProps)(App);
